import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiTypeService } from 'src/app/sdk/hr/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Pegawai_type } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiTypeService: DirektoriPegawaiTypeService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }


    // sort(sort: { key: string; value: string }): void {
    //     this.sortKey = sort.key
    //     this.sortValue = sort.value
    //     this.getData()
    // }


    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    listOfDisplayData: Pegawai_type[] = []
    listOfData: Pegawai_type[] = []
    getData() {
        this.spinner.show()
        this.direktoriPegawaiTypeService.getDirektoriPegawaiTypes(JSON.stringify({ sortKey: this.sortKey, sortValue: this.sortValue, search: this.search })).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()
            }
        )
    }

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }



    updateFilter(value: string): void {
        this.search = value
        this.getData()
    }

    delete(id) {
        this.spinner.show();
        this.direktoriPegawaiTypeService.deleteDirektoriPegawaiTypesId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
